import { Component, OnInit, Input } from "@angular/core";
import { NormalService } from 'src/app/normal.service';

declare const Swiper;

@Component({
    selector: 'messages',
    styleUrls: ["./messages.style.scss"],
    templateUrl: './messages.template.html',
})
export class MessagesComponent implements OnInit {
    @Input() gameId;
    messages: any[] = [];
    notices: any[] = [];
    messageIndex: number = 1;
    messagePageNumber: number = 1;
    noticeIndex: number = 1;
    noticePageNumber: number = 1;
    noticeLimit: number = 5;
    constructor(
        public normalService: NormalService,
    ){
        
    }
    ngOnInit() {
        this.getMessage();
        this.getNotice();
    }

    getMessage() {
        this.normalService.getMessages({page_index: this.messageIndex})
            .subscribe(messages=>{
                if (messages.code == 200) {
                    this.messages = messages.data.Records
                    this.messageIndex = messages.data.PageIndex;
                    this.messagePageNumber = Math.ceil(messages.data.Total/messages.data.ItemsPerPage);
                }
            })
    }

    getNotice() {
        this.normalService.getNotices(false,{page_limit: this.noticeLimit})
            .subscribe(notices=>{
                if (notices.code == 200) {
                    this.notices = notices.data.Records;
                }
            })
    }

    prevPageMessage() {
        this.messageIndex-=1;
        this.getMessage();
    }
    nextPageMessage() {
        this.messageIndex+=1;
        this.getMessage();
    }
    changeLimit() {
        this.getNotice();
    }
}