import { Component, OnInit, Input } from "@angular/core";
import { NormalService } from 'src/app/normal.service';
import { PageEvent, MatTableDataSource } from '@angular/material';
import { Router, ActivatedRoute } from '@angular/router';

declare const Swiper;

@Component({
    selector: 'bet-records',
    styleUrls: ["./betRecords.style.scss"],
    templateUrl: './betRecords.template.html',
})
export class BetRecordsComponent implements OnInit {
    orderType: string = '0';
    pageIndex: number = 0;
    length: number = 0;
    pageSize: number = 10;
    previousPageIndex: number = 0;
    // MatPaginator Output
    pageEvent: PageEvent;
    displayedColumns: string[] = ['GameName', 'GamePeriod','subName', 'BetMore','betReward','WinAmount','Ctime'];
    dataSource = new MatTableDataSource<any>([]);
    constructor(
        public normalService: NormalService,
        private activatedRoute: ActivatedRoute
    ){
        activatedRoute.params.subscribe(params=>{
            if (params.type) {
                this.orderType = params.type;
            }
        });
    }
    ngOnInit() {
        this.getRecords();
    }

    getRecords() {
        this.normalService.getHistoryBets({
            orderType: Number(this.orderType),
            pageIndex: this.pageIndex,
            pageLimit: this.pageSize
        })
            .subscribe(res=>{
                console.log("records:", res);
                this.length = res.data.Total;
                this.dataSource.data = res.data.Records;
            })
    }

    pageChange(pageEvent) {
        this.pageIndex = pageEvent.pageIndex;
        this.pageSize = pageEvent.pageSize;
        this.previousPageIndex = pageEvent.previousPageIndex;
        this.getRecords();
    }

    orderTypeChange(event) {
        let orderType = event.value;
        this.getRecords();
    }
}