import { Component, OnInit, Input } from "@angular/core";
import { NormalService } from 'src/app/normal.service';
import { PageEvent, MatTableDataSource } from '@angular/material';

declare const Swiper;

@Component({
    selector: 'account-records',
    styleUrls: ["./accountRecords.style.scss"],
    templateUrl: './accountRecords.template.html',
})
export class AccountRecordsComponent implements OnInit {
    dataType: string = '1';
    pageIndex: number = 0;
    length: number = 0;
    pageSize: number = 10;
    previousPageIndex: number = 0;
    // MatPaginator Output
    pageEvent: PageEvent;
    displayedColumns: string[] = ['OrderId', 'Coin', 'FreezeCoin','Balance', 'Info','ActionIp','Ctime'];
    dataSource = new MatTableDataSource<any>([]);
    constructor(
        public normalService: NormalService,
    ){
        
    }
    ngOnInit() {
        this.getRecords();
    }

    getRecords() {
        this.normalService.getCoinLogs({
            dataType: Number(this.dataType),
            pageIndex: this.pageIndex,
            pageLimit: this.pageSize
        })
            .subscribe(res=>{
                console.log("coinlogs:", res);
                this.length = res.data.Total;
                this.dataSource.data = res.data.Records;
            })
    }

    dataTypeChange(event) {
        let dataType = event.value;
        this.getRecords();
    }

    pageChange(pageEvent) {
        this.pageIndex = pageEvent.pageIndex;
        this.pageSize = pageEvent.pageSize;
        this.previousPageIndex = pageEvent.previousPageIndex;
        this.getRecords();
    }
}