import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from '@angular/router';
import { NormalService } from '../../normal.service';
import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { PageEvent } from '@angular/material';

declare const Swiper;

@Component({
    selector: 'inoutRecords',
    templateUrl: './inoutRecords.template.html',
    styleUrls: ['./inoutRecords.style.scss']
})
export class InoutRecordsComponent implements OnInit {
    isHandset$: Observable<boolean> = this.breakpointObserver.observe(Breakpoints.Handset)
    .pipe(
      map(result => result.matches)
    );
    type: number = 1;

    pageIndexIn: number = 0;
    lengthIn: number = 0;
    pageSizeIn: number = 10;
    previousPageIndexIn: number = 0;
    pageIndexOut: number = 0;
    lengthOut: number = 0;
    pageSizeOut: number = 10;
    previousPageIndexOut: number = 0;
    // MatPaginator Output
    pageEvent: PageEvent;

    outRecords: any[]  = [];
    inRecords: any[]  = [];
    constructor (
        public normalService: NormalService,
        private breakpointObserver: BreakpointObserver,
        private activatedRoute: ActivatedRoute,
        private router: Router
    ) {
        this.activatedRoute.params.subscribe(params=>{
            this.type = params.type;
            if (this.type == 1) {
                this.readInRecords();
            }else {
                this.readOutRecords();
            }
            this.normalService.getChargeAccounts().subscribe(res=>{
                this.inRecords.forEach(record => {
                    res.data.Records.forEach(element => {
                        if (record.ResaveAccountId = element.Id) {
                            record.ResaveAccount = element;
                        }
                    });
                })
            })
        })
    }
    ngOnInit() {
    }

    changeTab(event) {
        this.router.navigate(['/user/inoutRecords/'+(event+1)])
    }

    pageChangeIn(pageEvent) {
        this.pageIndexIn = pageEvent.pageIndex;
        this.pageSizeIn = pageEvent.pageSize;
        this.previousPageIndexIn = pageEvent.previousPageIndex;
        this.readInRecords();
    }

    pageChangeOut(pageEvent) {
        this.pageIndexOut = pageEvent.pageIndex;
        this.pageSizeOut = pageEvent.pageSize;
        this.previousPageIndexOut = pageEvent.previousPageIndex;
        this.readOutRecords();
    }

    readInRecords() {
        this.normalService.getDepositList()
            .subscribe(res=>{
                if (res.code == 200) {
                    this.lengthIn = res.data.Total;
                    this.inRecords = res.data.Records;
                }
            })
    }

    readOutRecords() {
        this.normalService.getWithdrawList()
            .subscribe(res=>{
                if (res.code == 200) {
                    this.lengthOut = res.data.Total;
                    this.outRecords = res.data.Records;
                }
            })
    }

    showDetail(record) {
        record.showDetail = !record.showDetail;
    }
}