import { Component, OnInit, Input } from "@angular/core";
import { NormalService } from 'src/app/normal.service';
import { MatSnackBar } from '@angular/material';
import {Md5} from "ts-md5";

declare const Swiper;

@Component({
    selector: 'password',
    styleUrls: ["./password.style.scss"],
    templateUrl: './password.template.html',
})
export class PasswordComponent implements OnInit {
    passwordModel: any = {};
    payPasswordModel: any = {};

    tabIndex: number = 0;

    constructor(
        public normalService: NormalService,
        private snackbar: MatSnackBar,
    ){
        
    }
    ngOnInit() {
        this.passwordModel.platform = this.normalService.platform;
        this.payPasswordModel.platform = this.normalService.platform;
    }

    changeTab(event) {
        this.tabIndex = event;
        this.resetPassword();
        this.resetPayPassword();
    }

    resetPassword() {
        this.passwordModel.new_password = '';
        this.passwordModel.re_new_password = '';
        this.passwordModel.password = '';
    }

    resetPayPassword() {
        this.payPasswordModel.new_pay_password = '';
        this.payPasswordModel.re_new_pay_password = '';
        this.payPasswordModel.pay_password = '';
    }

    updatePassword() {
        if (this.passwordModel.new_password !== this.passwordModel.re_new_password) {
            this.snackbar.open("两次的密码不一致，请重新输入", "确定",{
                duration: 2000
            })
            return;
        }
        this.passwordModel.password = Md5.hashStr(this.passwordModel.password).toString();
        this.passwordModel.new_password = Md5.hashStr(this.passwordModel.new_password).toString();
        this.normalService.updatePassword(this.passwordModel)
            .subscribe(res=>{
                if (res.code == 200) {
                    this.snackbar.open(res.message, "确定", {
                        duration: 2000
                    });
                    this.resetPassword();
                } else {
                    this.snackbar.open(res.message, "确定", {
                        duration: 2000
                    })
                }
            })
    }

    updatePayPassword() {
        if (this.payPasswordModel.new_pay_password !== this.payPasswordModel.re_new_pay_password) {
            this.snackbar.open("两次的密码不一致，请重新输入", "确定",{
                duration: 2000
            })
            return;
        }
        this.normalService.updatePayPassword(this.payPasswordModel)
            .subscribe(res=>{
                console.log("paypassword res:", res);
                if (res.code == 200) {
                    this.snackbar.open(res.message, "确定", {
                        duration: 2000
                    })
                    this.resetPayPassword();
                } else {
                    this.snackbar.open(res.message, "确定", {
                        duration: 2000
                    })
                }
            })
    }
}