import { Component, OnInit, Input } from "@angular/core";
import { NormalService } from 'src/app/normal.service';
import { PageEvent, MatTableDataSource } from '@angular/material';
import { Router, ActivatedRoute } from '@angular/router';

declare const Swiper;

@Component({
    selector: 'count-sum',
    styleUrls: ["./countSum.component.scss"],
    templateUrl: './countSum.component.html',
})
export class CountSumComponent implements OnInit {
    length: number = 0;
    displayedColumns: string[] = ['Uid', 'Username', 'Enable', 'Grade', 'Type', 'Sbets', 'Swins', 'Sloss','Score', 'action'];
    dataSource = new MatTableDataSource<any>([]);
    sequence: string = "month";
    SumSbets: number;
    SumSloss: number;
    SumSwins: number;
    constructor(
        public normalService: NormalService,
        private activatedRoute: ActivatedRoute
    ){
    }
    ngOnInit() {
        this.getRecords();
    }

    getRecords() {
        this.normalService.ChildBetsSum(this.sequence)
            .subscribe(res=>{
                console.log("ChildBetsSum:", res);
                this.length = res.data.Children.length;
                this.dataSource.data = res.data.Children;
                this.SumSbets = res.data.SumSbets;
                this.SumSloss = res.data.SumSloss;
                this.SumSwins = res.data.SumSwins;
            })
    }

    togglePeriod(period): void {
        this.sequence = period;
        this.getRecords();
    }

    orderTypeChange(event) {
        let orderType = event.value;
        this.getRecords();
    }
}