import { Component, OnInit, Input } from "@angular/core";
import { NormalService } from 'src/app/normal.service';
import { PageEvent, MatTableDataSource } from '@angular/material';
import { Router, ActivatedRoute } from '@angular/router';

declare const Swiper;

@Component({
    selector: 'weeklists',
    templateUrl: './weeklists.component.html',
})
export class WeeklistsComponent implements OnInit {
    pageIndex: number = 0;
    length: number = 0;
    pageSize: number = 10;
    previousPageIndex: number = 0;
    displayedColumns: string[] = ['Id','Type', 'BetSum', 'WinSum', 'LossSum', 'Ctime', 'PayTime', 'action'];
    dataSource = new MatTableDataSource<any>([]);
    constructor(
        public normalService: NormalService,
        private activatedRoute: ActivatedRoute
    ){
    }
    ngOnInit() {
        this.getRecords();
    }

    getRecords() {
        this.normalService.WeekSumList({})
            .subscribe(res=>{
                console.log('WeekSumList:', res);
                this.length = res.data.Total;
                this.dataSource.data = res.data;
            })
    }

    pageChange(pageEvent) {
        this.pageIndex = pageEvent.pageIndex;
        this.pageSize = pageEvent.pageSize;
        this.previousPageIndex = pageEvent.previousPageIndex;
        this.getRecords();
    }

    orderTypeChange(event) {
        let orderType = event.value;
        this.getRecords();
    }
}