import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';

import {RouterModule} from '@angular/router';
import {FormsModule} from "@angular/forms";
import { UserComponent } from './user.component';
import { MessagesComponent } from './messages/messages.component';
import { AccountRecordsComponent } from './accountRecords/accountRecords.component';
import { BankCardComponent } from './bankcard/bankcard.component';
import { BetRecordsComponent } from './betRecords/betRecords.component';
import { PasswordComponent } from './password/password.component';
import { RecommendComponent } from './recommend/recommend.component';
import { FlexLayoutModule } from '@angular/flex-layout';
import { LayoutComponent } from './layout/layout.component';
import { MatListModule, MatDividerModule, MatTabsModule, MatStepperModule, MatFormFieldModule, MatInputModule, MatButtonModule, MatPaginatorModule, MatRadioModule, MatTableModule, MatTooltipModule, MatDialogModule } from '@angular/material';
import { DepositComponent } from './deposit/deposit.component';
import { WithdrawComponent } from './withdraw/withdraw.component';
import { ServiceComponent } from './service/service.component';
import { InoutRecordsComponent } from './inoutRecords/inoutRecords.component';
import { CommonDialog } from '../common_dialog/common_dialog.component';
import { RefererComponent } from './referer/referer.component';
import { CountSumComponent } from './countSum/countSum.component';
import { WeeklistsComponent } from './weeklists/weeklists.component';


export const routes = [
    {path: '', component: LayoutComponent, children: [
        {path: '', redirectTo: 'lobby', pathMatch: 'full'},
        {path: 'lobby', component: UserComponent},
        {path: 'deposit', component: DepositComponent},
        {path: 'withdraw', component: WithdrawComponent},
        {path: 'messages', component: MessagesComponent},
        {path: 'messages/:id', component: MessagesComponent},
        {path: 'account_records', component: AccountRecordsComponent},
        {path: 'account_records/:type', component: AccountRecordsComponent},
        {path: 'bankcard', component: BankCardComponent},
        {path: 'bet_records', component: BetRecordsComponent},
        {path: 'bet_records/:type', component: BetRecordsComponent},
        {path: 'password', component: PasswordComponent},
        {path: 'password/:pay', component: PasswordComponent},
        {path: 'recommend', component: RecommendComponent},
        {path: 'service', component: ServiceComponent},
        {path: 'inoutRecords', redirectTo: 'inoutRecords/1', pathMatch: 'full'},
        {path: 'inoutRecords/:type', component: InoutRecordsComponent},
        {path: 'inoutRecords/:type/:id', component: InoutRecordsComponent},
        {path: 'referer', component: RefererComponent},
        {path: 'countSum', component: CountSumComponent},
        {path: 'weeklists', component: WeeklistsComponent}
    ]}
];

@NgModule({
    imports: [CommonModule, RouterModule.forChild(routes), FormsModule,
        FlexLayoutModule,
        MatListModule,
        MatDividerModule,
        MatTabsModule,
        MatStepperModule,
        MatFormFieldModule,
        MatInputModule,
        MatButtonModule,
        MatPaginatorModule,
        MatRadioModule,
        MatTableModule,
        MatTooltipModule,
        MatDialogModule,
    ],
    entryComponents: [CommonDialog],
    declarations: [
        LayoutComponent,
        UserComponent,
        MessagesComponent,
        AccountRecordsComponent,
        BankCardComponent,
        BetRecordsComponent,
        PasswordComponent,
        RecommendComponent,
        DepositComponent,
        WithdrawComponent,
        ServiceComponent,
        InoutRecordsComponent,
        CommonDialog,
        RefererComponent,
        CountSumComponent,
        WeeklistsComponent
    ],
})
export class UserModule {
    static routes = routes;
}
