import { Component, OnInit, ElementRef } from "@angular/core";
import { ActivatedRoute, Router } from '@angular/router';
import { NormalService } from '../../normal.service';
import { MatSnackBar } from '@angular/material';
import { element } from '@angular/core/src/render3';

declare const Swiper;

@Component({
    selector: 'deposit',
    templateUrl: './deposit.template.html',
    styleUrls: ['./deposit.style.scss']
})
export class DepositComponent implements OnInit {
    chargeAccounts: any[] = [];
    selectedAccountId: number;
    depositModel: any = {}
    channel: number = 0;
    stepperIndex: number = 0;

    depositOrder: any = {};
    zhifubao: any = {};
    pay_bankcode: any;

    paying: boolean = false;

    payAmountZhifubao: number = 500;
    payAmountWeixin: number = 100;
    payAmountWeixinSaoma: number = 500;
    
    constructor (
        public normalService: NormalService,
        private snackbar: MatSnackBar,
        private router: Router,
        private el: ElementRef
    ) {
    }
    ngOnInit() {
        this.normalService.getChargeAccounts()
            .subscribe(acc=>{
                this.chargeAccounts = acc.data.Records;
            })
    }

    choseAccount(account) {
        this.selectedAccountId = account.Id;
        this.chargeAccounts.forEach(a=>{
            a.selected = false;
        });
        account.selected = true;
    }

    createDeposit() {
        if (!this.selectedAccountId) {
            this.snackbar.open("请先选择汇款账户", "确定", {
                duration: 2000,
              });
            return
        }
        this.depositModel.channel = this.channel+1;
        this.depositModel.resave_account_id = this.selectedAccountId;
        this.depositModel.numeric = Number((this.depositModel.numeric+Number(Math.random().toFixed(2))).toFixed(2));
        this.normalService.deposit(this.depositModel)
            .subscribe(res=>{
                if (res.code == 200) {
                    this.depositOrder = res.data;
                    this.chargeAccounts.forEach(element=>{
                        if (element.Id == this.depositOrder.ResaveAccountId) {
                            this.depositOrder.Resaver = element;
                        }
                    })
                    this.snackbar.open("充值订单创建成功，"+res.message, "确定", {
                        duration: 2000,
                      })
                    this.stepperIndex++;
                }
            })
    }

    changeChannel(event) {
        this.paying = false;
        if (event == 1) { // 支付宝
            console.log("支付宝");
            this.pay_bankcode = "903";
        } else if (event == 2) { // 微信支付
            console.log("微信支付");
            this.pay_bankcode = "901";
        } else if (event == 3) { // 微信扫码支付
            console.log("微信扫码支付");
            this.pay_bankcode = "902";
        } else if (event == 5) { // QQ支付
            console.log("QQ支付");

        }
    }

    baseCheck() {
        if (!this.depositModel.pay_account || !this.depositModel.pay_bank || !this.depositModel.numeric) {
            this.snackbar.open("所填信息不完整，请完善信息后再试", "确定", {
                duration: 2000,
              });
            return
        }
        this.stepperIndex++;
    }

    backStep() {
        this.stepperIndex--;
    }

    confirmPayed() {
        // 可以请求服务端，提醒客服监控
        this.router.navigate(['/user/inoutRecords/1'])
    }

    submitZhifubao() {
        console.log("zhifubao", this.zhifubao);
        console.log("submitZhifubao", this.el.nativeElement.querySelector('#form1 input'));
        this.paying = true;
        this.normalService.createDepositThird({pay_amount: String(this.payAmountZhifubao), pay_bankcode: this.pay_bankcode})
            .subscribe(res=>{
                console.log("支付宝：", res);
                this.zhifubao = res.data;
                setTimeout(()=>{
                    this.el.nativeElement.querySelector('#formZhifubao').submit();
                },500)
            })
    }
    submitWeixin() {
        this.paying = true;
        this.normalService.createDepositThird({pay_amount: String(this.payAmountWeixin), pay_bankcode: this.pay_bankcode})
            .subscribe(res=>{
                console.log("微信：", res);
                this.zhifubao = res.data;
                setTimeout(()=>{
                    this.el.nativeElement.querySelector('#formWeixin').submit();
                },500)
            })
    }
    submitWeixinSaoma() {
        this.paying = true;
        this.normalService.createDepositThird({pay_amount: String(this.payAmountWeixinSaoma), pay_bankcode: this.pay_bankcode})
            .subscribe(res=>{
                console.log("微信扫码：", res);
                this.zhifubao = res.data;
                setTimeout(()=>{
                    this.el.nativeElement.querySelector('#formWeixinSaoma').submit();
                },500)
            })
    }
}