import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from '@angular/router';
import { NormalService } from '../../normal.service';
import { MatSnackBar, MatDialog } from '@angular/material';
import { CommonDialog } from 'src/app/common_dialog/common_dialog.component';

declare const Swiper;

@Component({
    selector: 'withdraw',
    templateUrl: './withdraw.template.html',
    styleUrls: ['./withdraw.style.scss']
})
export class WithdrawComponent implements OnInit {
    accounts: any[] = [];
    withdrawModel: any = {
        channel: 1
    };
    stepperIndex: number = 0;
    constructor (
        public normalService: NormalService,
        private snackbar: MatSnackBar,
        private router: Router,
        public dialog: MatDialog,
    ) {
    }
    ngOnInit() {
        this.getWithdrawAccounts();
    }

    getWithdrawAccounts() {
        this.normalService.getAccountList({})
            .subscribe(res=>{
                if (res.code == 200) {
                    this.accounts = res.data.Records;
                }
            })
    }

    choseAccount(account) {
        this.accounts.forEach(a=>{
            a.selected = false;
        });
        account.selected = true;
        this.withdrawModel.member_account_id = account.Id;
    }

    confirmWithdraw() {
        if (!this.withdrawModel.pay_password) {
            this.snackbar.open("资金密码不能为空","确定", {
                duration: 3000,
              });
            return;
        }
        this.normalService.withdraw(this.withdrawModel)
            .subscribe(res=>{
                if (res.code !== 200) {
                    this.snackbar.open(res.message, "确定", {
                        duration: 3000,
                      })
                } else {
                    this.normalService.User.Coin = res.data.balance;
                    this.snackbar.open("取款订单提交成功", "确定", {
                        duration: 3000,
                      })
                    this.router.navigate(['/user/inoutRecords/2'])
                }
            })
    }

    addAccountDialog() {
        let commonDialog = this.dialog.open(CommonDialog, {
            width: "250px",
            data: {
                title: "新建收款账号",
                data: [
                    {
                        name: "real_name",
                        label: "收款人",
                        type: "text",
                        disabled: false
                    },
                    {
                        name: "konto_number",
                        label: "收款账号",
                        type: "text",
                        disabled: false
                    },
                    {
                        name: "bank_name",
                        label: "收款银行",
                        type: "text",
                        disabled: false
                    },
                    {
                        name: "bank_address",
                        label: "开户行地址",
                        type: "text",
                        disabled: false
                    },
                    {
                        name: "pay_password",
                        label: "资金密码",
                        type: "text",
                        disabled: false
                    }
                ]
            }
          });
          commonDialog.afterClosed().subscribe(async data => {
            if (!data) return;
            let model: any = {};
            data.data.forEach(element => {
                model[element.name] = element.value;
            });
            this.addAccount(model);
          })
    }

    addAccount(data) {
        delete data.member_account_id;
        delete data.delete;
        this.normalService.updateMemberAccount(data)
            .subscribe(res=>{
                if (res.code !== 200) {
                    this.snackbar.open(res.message, "确定", {
                        duration: 3000,
                      });
                } else {
                    this.snackbar.open("收款账号添加成功","确定", {
                        duration: 3000,
                      })
                    this.accounts.unshift(res.data)
                }
            })
    }

    removeAccountDialog(account) {
        let commonDialog = this.dialog.open(CommonDialog, {
            width: "250px",
            data: {
                title: "确认资金密码",
                data: [
                    {
                        name: "konto_number",
                        label: "要删除的收款账号",
                        type: "text",
                        value: account.KontoNumber,
                        disabled: true
                    },
                    {
                        name: "member_account_id",
                        value: account.Id,
                        disabled: true,
                        hidden: true
                    },
                    {
                        name: "pay_password",
                        label: "资金密码",
                        type: "text",
                        disabled: false
                    }
                ]
            }
          });
          commonDialog.afterClosed().subscribe(async data => {
            if (!data) return;
            let model: any = {};
            data.data.forEach(element => {
                model[element.name] = element.value;
            });
            console.log("model", model);
            this.removeAccount(model);
          })
    }

    removeAccount(data) {
        data.delete = true;
        this.normalService.updateMemberAccount(data)
            .subscribe(res=>{
                if (res.code == 200) {
                    this.accounts.forEach((a, i)=>{
                        if (a.Id == data.member_account_id) {
                            this.accounts.splice(i, 1)
                        }
                    })
                    this.snackbar.open("收款账号已删除","确定", {
                        duration: 3000,
                      })
                }
            })
    }

    editAccountDialog(account) {
        let commonDialog = this.dialog.open(CommonDialog, {
            width: "250px",
            data: {
                title: "编辑收款账号",
                data: [
                    {
                        name: "real_name",
                        label: "收款人",
                        value: account.ResaverName,
                        disabled: false
                    },
                    {
                        name: "konto_number",
                        label: "收款账号",
                        value: account.KontoNumber,
                        disabled: false
                    },
                    {
                        name: "bank_name",
                        label: "收款银行",
                        value: account.BankName,
                        disabled: false
                    },
                    {
                        name: "bank_address",
                        label: "收款开户行",
                        value: account.BankAddress,
                        disabled: false
                    },
                    {
                        name: "member_account_id",
                        value: account.Id,
                        disabled: true,
                        hidden: true
                    },
                    {
                        name: "pay_password",
                        label: "资金密码",
                        type: "text",
                        disabled: false
                    }
                ]
            }
        });
        commonDialog.afterClosed().subscribe(async data => {
        if (!data) return;
        let model: any = {};
        data.data.forEach(element => {
            model[element.name] = element.value;
        });
        console.log("model", model);
        this.updateAccount(model);
        })
    }

    updateAccount(data) {
        data.delete = false;
        this.normalService.updateMemberAccount(data)
            .subscribe(res=>{
                if (res.code == 200) {
                    this.accounts.forEach((acount, index)=>{
                        if (acount.Id == res.data.Id) {
                            this.accounts[index] = res.data;
                        }
                    })
                    this.snackbar.open("收款账户修改成功", "确定", {
                        duration: 3000,
                      });
                } else {
                    this.snackbar.open(res.message, "确定", {
                        duration: 3000,
                      })
                }
            })
    }

    baseCheck(step) {
        switch (step) {
            case 0:
                if (!this.withdrawModel.member_account_id) {
                    this.snackbar.open("请先选择收款账户", "确定", {
                        duration: 3000,
                      });
                    return
                }
                break;
            case 1:
                if (!this.withdrawModel.numeric) {
                    this.snackbar.open("提款金额为必填！", "确定", {
                        duration: 3000,
                      })
                    return;
                }
                if (this.withdrawModel.numeric > this.normalService.User.Coin) {
                    this.snackbar.open("提款金额必须小于账户可用余额！", "确定", {
                        duration: 3000,
                      });
                    return;
                }
                break;
            default:
                break;
        }
        this.stepperIndex++;
    }

    changeStep(event) {

    }
}